* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto;
    font-style: normal;
}

.container {
    max-width: 1264px;
    padding: 72px 16px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;

}

.column {
    width: 50%;
}

.column-6 {
    width: 16.6%;
}

/*header nav block*/

.header .container {
    padding-top: 40px;
}

.header__nav {
    margin-bottom: 40px;
}

.nav {
    display: flex;
    justify-content: space-around;
}

.nav a {
    color: #3D3B3C;
    font-size: 24px;
    text-decoration: none;
}

.nav a:hover {
    transform: scale(1.2);
}


.title {
    text-align: center;
    font-weight: 500;
    font-size: 56px;
    color: #120D0E;
    margin-bottom: 56px;
}

.header__line {
    height: 1px;
    color: #3D3B3C;
    margin-bottom: 72px;
}

.header__title {
    font-weight: 500;
    font-size: 56px;
    color: #120D0E;
}

.header__title--dev {
    font-weight: 500;
    font-size: 56px;
    color: #BEBEBE;
    margin-bottom: 40px;
}

.header__subtitle {
    font-size: 22px;
    line-height: 26px;
    color: #3D3B3C;
    margin-bottom: 72px;
    max-width: 576px;
}

.header__btn {
    padding: 14px 20px;
    font-weight: 500;
    font-size: 24px;
    color: #FFFFFF;
    background: #3A4576;
    cursor: pointer;
}

.header__btn:first-of-type {
    margin-right: 16px;
}

.header__btn:hover {
    transform: scale(1.1);
}

.header__photo {
    max-width: 412px;
    max-height: 464px;
    float: right;
}

/*skills block*/
.skill__elem {
    text-align: center;
}

.skill__photo {
    height: 100px;
}

.skill__title {
    margin: 15px;
}

.skill__v {
    justify-content: center;
    cursor: pointer;
}

.skill__elem {
    display: none;
}

.skill__elem:first-of-type {
    display: block;
}

.skill__v:hover {
    transform: scale(1.4);
}

.skills__activ {
    transform: rotate(180deg);
}


/*project block*/
.awssld {
    --organic-arrow-color: #ff6207;
    --slider-height-percentage: 35%;
}

.awssld__content {
    display: block;
    background-color: #FFFFFF;
}

.project__slide {
    max-height: 100%;
    max-width: 100%;
    display: flex;
    align-items: center;
}

.project__img {
    max-width: 90%;
    max-height: 90%;
    margin-right: 130px;
}

.project__title {
    font-weight: 500;
    font-size: 24px;
    color: #120D0E;
}

.project__subtitle {
    font-size: 24px;
    color: #BEBEBE;
    max-width: 497px;
    margin-bottom: 72px;
}

.project__link {
    font-weight: 500;
    font-size: 24px;
    color: #FFFFFF;
    text-decoration: none;
    padding: 14px 17px;
    background-color: #3A4576;
}

.project__link:hover {
    background-color: #2EE59D;
    box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
    color: #fff;
    transform: translateY(-7px);
}

/*education block*/


.education {
    background-color: #3dc6ee;
}

/*lang block*/

.lang-center {
    text-align: center;
}

.lang__photo {
    margin-bottom: 24px;
}

.lang__title {
    font-weight: 500;
    font-size: 24px;
    color: #120D0E;
}

.lang__subtitle {
    font-size: 24px;
    color: #120D0E;
}

/*contact block*/

.contact__photo {
    max-width: 412px;
    max-height: 412px;
    border-radius: 30%;
    object-fit: cover;
}

.contact__col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact__link {
    margin-right: 16px;
}

.contact__links {
    margin-bottom: 105px;
}

.contact__photo:hover {
    box-shadow: 0 0 11px rgba(33, 33, 33, .2);
    transform: scale(1.1);
}

.contact__link:last-of-type {
    margin-right: 0;
}

.contact__title {
    font-size: 24px;
    text-decoration-line: underline;
    color: #000000;
}

.contact__subtitle {
    font-size: 24px;
    text-decoration-line: none;
    color: #000000;
}

@media only screen and (max-width: 900px) {
    .project__slide {
        flex-direction: column;
    }

    .awssld {
        --slider-height-percentage: 80%;
    }
}

@media only screen and (max-width: 768px) {

    .header__photo {
        float: none;
    }

    .header__subtitle {
        margin: 20px auto;
    }

    .header__reverse {
        flex-direction: column-reverse;
    }


    .project__col {
        margin: 30px;
    }

    .project__title {
        display: none;
    }

    .project__subtitle {
        display: none;
    }

    .col-3 {
        width: 33%;
    }

    .lang-center {
        margin-bottom: 30px;
    }

    .col-1 {
        width: 100%;
    }

    .contact__photo {
        margin-bottom: 40px;
    }

    .contact__links {
        margin-bottom: 30px;
    }
}

@media only screen and (max-width: 480px) {
    .col-3 {
        width: 50%;
    }
}
